import { Union, Record } from "./.fable/fable-library.3.0.0/Types.js";
import { defaultPage, Page$reflection } from "./Router.js";
import { union_type, record_type, bool_type } from "./.fable/fable-library.3.0.0/Reflection.js";

export class Model extends Record {
    constructor(CurrentPage, ShowQuickView, ShowLoader) {
        super();
        this.CurrentPage = CurrentPage;
        this.ShowQuickView = ShowQuickView;
        this.ShowLoader = ShowLoader;
    }
}

export function Model$reflection() {
    return record_type("Docs.Domain.Model", [], Model, () => [["CurrentPage", Page$reflection()], ["ShowQuickView", bool_type], ["ShowLoader", bool_type]]);
}

export const ModelModule_init = new Model(defaultPage, false, false);

export class Msg extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["UrlChanged", "ToggleQuickView", "ToggleLoader"];
    }
}

export function Msg$reflection() {
    return union_type("Docs.Domain.Msg", [], Msg, () => [[["Item", Page$reflection()]], [], []]);
}

